import React, { useRef } from "react"
import { createPortal } from "react-dom"
import { MdClose } from "react-icons/md"

import useCreatePortal from "../../../hooks/useCreatePortal"
import useClickOutside from "../../../hooks/useClickOutside"
import { nanoid } from "nanoid"
import "./style.scss"

const Drawer = ({
  children,
  callback = () => {},
  styles = {},
  type = null,
  open = false,
  NamePortal = "Portal",
}) => {
  const isRender = useCreatePortal(NamePortal)

  const ref = useRef(null)
  useClickOutside(ref, () => callback())

  return isRender && open
    ? typeof document !== "undefined" &&
        document.getElementById(NamePortal) &&
        createPortal(
          <div
            className={`${
              type !== "bottom" ? "drawer-Wrapper" : "drawer-Wrapper-bottom"
            }`}
            style={styles}
          >
            <div ref={ref} className="drawer-Container">
              <div className="drawer-header p-2">
                <button
                  className={"btn-clear btn-animate text-white  ml-auto"}
                  onClick={callback}
                >
                  <MdClose size="28" className="" />
                </button>
              </div>

              {children}
            </div>
          </div>,
          document.getElementById(NamePortal)
        )
    : null
}

export default Drawer
